import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';

import MetaTag from '../meta/MetaTag';
import { SETTING_PROGRESS, PRACTICE_PROGRESS, RESULT_PROGRESS } from '../modules/practiceProgress';

import Header from '../commons/Header';
import Footer from '../commons/Footer';
import PracticeSettings from '../components/practice-settings/PracticeSettings';
import PracticePaper from '../components/practice-paper/PracticePaper';
import PracticeResult from '../components/practice-result/PracticeResult';

const PracticePageContent = () => {
    const { practiceProgress } = useSelector(state => ({
        practiceProgress: state.practiceProgress,
    }));

    const progressName = practiceProgress.name;
    if (progressName === SETTING_PROGRESS.name) {
        return <PracticeSettings />;
    } else if (progressName === PRACTICE_PROGRESS.name) {
        return <PracticePaper />;
    } else if (progressName === RESULT_PROGRESS.name) {
        return <PracticeResult />;
    } else {
        return null;
    }
};

const StyledDiv = styled.div`
    display: flex;
    flex-direction: column;
    min-height: ${props => props.height}px;
`;

const StyledContentDiv = styled.main`
    flex: 1;
    overflow: auto;
`;

const PracticePage = () => {
    return (
        <StyledDiv height={window.innerHeight}>
            <MetaTag 
                title=" "
                keywords={", , ,  ,  , , , , , "
                    + ", , , , , , , , , "}
                description="    .         ."
                url="https://appre.github.io/arithmetic/"
            />

            <Header />
            <StyledContentDiv>
                <PracticePageContent />
            </StyledContentDiv>
            <Footer />
        </StyledDiv>
    );
};

export default PracticePage;